import Axios from 'axios'

export default  {
  name: 'app-cadastrar',
  components: {},
  props: [],
  data () {
    return {
      nome: '',
      email: '',
      senha: '',
      cpf: '',
      cnh: '',
      cidade: '',
      endereco: '',
      telefone: '',
      dataNasc: ''
    }
  },
  computed: {

  },
  mounted () {

  },
  methods: {
    salvar(){
      var config = {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      };
      let data = new FormData();
      data.append('nome', this.nome);
      data.append('email', this.email);
      data.append('senha', this.senha);
      data.append('cpf', this.cpf);
      data.append('cnh', this.cnh);
      const http = Axios.post('http://localhost/caronas/cadastrarUsuario.php',data);
      http.then(response => {console.log(response);});
      http.catch(error => {console.log(error);});
    }

  }
}



