import Axios from 'axios'

export default  {
  name: 'app-lista-clientes',
  components: {},
  props: [],
  data () {
    return {
      usuarios: []
    }
  },
  computed: {

  },
  mounted () {

  },
  methods: {
  listar(){
    const http = Axios.get('http://localhost/caronas/listarUsuario.php');
    http.then(response => {
      console.log(response.data);
      this.usuarios = response.data;
    });
    http.catch(error => {console.log(error);});
  }

  }
}



