import Vue from 'vue'
import Router from 'vue-router'
import CadastraBuscaCarona from '@/components/appCadastraBuscaCarona'
// import Cidade from '@/components/appCidades'
import Postos from '@/components/appPostos'
// import ConsultaGasolina from '@/components/appConsultaGasolina'
import CadastraCarona from '@/components/appCadastraCarona'
import ListaPostos from '@/components/appListaPostos'
import ListaPreco from '@/components/appListaPrecos'
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Cadastra Busca Carona',
      components: {
        default: CadastraBuscaCarona,
        BuscaGasolina: ListaPostos
      } // children routes
    },
    {
      path: '/listaPostos',
      component: ListaPostos,
      components: {
        default: CadastraBuscaCarona,
        ListaPreco: ListaPreco
      }
    },
    {
      path: '/cadastrarCarona',
      name: 'Cadastrar Carona',
      component: CadastraCarona
    },
    {
      path: '/postos',
      component: Postos
    }
  ]
})



